@import "../../../styles/helpers/_helpers";

.block-questions {
	.block__content {
		display: grid;
		grid-template-columns: repeat(var(--grid-column), 1fr);
		gap: var(--grid-gap);
		align-items: flex-start;
	}
	.block__info {
		grid-column: span 7;
		p {
			@include last(1) {
				margin-bottom: 0;
			}
		}
	}
	.most-questions {
		grid-column: 8/-1;
	}
}
